import Button from '@mui/material/Button';
import { ReactNode } from 'react';

interface ButtonProps {
    children?: string | ReactNode,
    disabled?: boolean,
    id?: string,
    className?: string,
    style?: object,
    onClick?: Function,
    endIcon?: ReactNode,
};

function ButtonComp({children, className, id, onClick, disabled, style, endIcon, ...rest}: ButtonProps) {

    const handleClick = (e: React.MouseEvent<HTMLButtonElement>) => {
        onClick && onClick(e);
    };

    return (
        <Button 
            id={id}
            className={className}
            onClick={handleClick}
            style={style}
            disabled={disabled}
            endIcon={endIcon}
            {...rest}
        >
            { children }
        </Button>
    )
}

export default ButtonComp;
